@-webkit-keyframes pulseSap {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes pulseSap {
  0% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.1);
    -ms-transform: scale(1.1);
    transform: scale(1.1);
  }

  100% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
  }
}

.pulseSap {
  -webkit-animation-name: pulseSap;
  animation-name: pulseSap;
}

.about-img {
  position: relative;

  img {
    width: 100%;
  }
}

.about-text {
  padding-top: 15%;

  h1 {
    color: #fff;
    font-family: lobster;
    font-size: 50px;
    font-weight: bold; 
    text-transform: none;
  }
  p {
    font-size: 18px;
    color: #ddd;
  }
}

.leftSide {
  animation: bounceInLeft;
  animation-duration: 2s;
}

.textAnimate {
  animation: pulseSap 20s infinite;
  text-align: center;
  padding: 20px 0;
}
