.Logo {
	img{
		width: 300px;
		height: 380px;
		display: flex;
		justify-content: center;
		margin: 0 auto;
	}
	.text_cont {
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100%;
		padding: 0 3rem;
		color: #fff;
		text-align: center;
		font-size: 23px;
	}
	.txt-type{
		margin: auto;
	}
	
	h1, h2 {
		font-weight: 200;
		margin: 0.4rem;
	}
	
	h1 {
		font-size: 21px!important;
	}
	
	h2 {
		font-size: 2rem;
		color: #aaa;
	}
	
	/* Cursor */
	.txt-type > .txt {
		border-right: 0.2rem solid #777;
		font-size: 2.5rem;
		color: white;
	}
	
	@media(min-width: 1200px) {
		h1 {
			font-size: 5rem;
		}
	}
	
	@media(max-width: 800px) {
		.text_cont {
			padding: 0 1rem;
		}
	
		.txt-type {
			font-size: 1rem;
		}
	}
	
	@media(max-width: 500px) {
		h1 {
			font-size: 21px!important;
		}
	
		h2 {
			font-size: 1.5rem;
		}		
	}
}