a {
  text-decoration: none;
}

nav {
  width: 90%;
  // margin: 0 auto;
  display: flex;
  justify-content: flex-end;
}

.lang-menu {
  margin-top: 25px;
  position: relative;
  &:hover {
    display: block;
  }
    ul {
      margin: 0;
      padding: 0;
      background-color: #0d101791;
      box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.2);
      display: none;
      position: absolute;
      top: 40px;
      left: 0;
      z-index: 99999;
    }
    li {
      list-style: none;
      display: flex;
      justify-content: space-between;
      cursor: pointer;
    }
    a {
      width: 140px;
      display: block;
      padding: 5px 10px;
      color: white;
        &:hover {
          background-color: #080707;
        }
        &::before {
          content: '';
          display: inline-block;
          vertical-align: middle;
          margin-right: 10px;
          width: 32px;
          height: 32px;
          background-image: url(./flags/tm.png);
        }
    }
    .tk::before {
      background-image: url(./flags/tm.png);
    }
    .en::before {
      background-image: url(./flags/us.png);
    }
    .ru::before {
      background-image: url(./flags/ru.png);
    }
}

.selected-lang {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  line-height: 2;
  width: 100px;
  color: white;
    &::before {
      content: '';
      display: block;
      width: 32px;
      height: 32px;
      background-image: url(./flags/tm.png);
    }
}
