.nav {
  position: fixed;
  bottom: 0;
  width: 70%;
  height: 55px;
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2),
							0 7px 20px 0 rgba(0, 0, 0, 0.17);
  background-color: #1a334605;
  backdrop-filter: blur(5px);
	-moz-backdrop-filter: blur(5px);
  border-radius: 50px;
  left: 15%;
  display: flex;
  overflow-x: auto;
  overflow-y: hidden;
  .dock_element {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      flex-grow: 1;
      min-width: 50px;
      overflow-y: hidden;
      overflow-x: auto;
      white-space: nowrap;
      font-family: sans-serif;
      font-size: 13px;
      color: #fff;
      text-decoration: none;
      -webkit-tap-highlight-color: transparent;
      transition: background-color 0.1s ease-in-out;
      cursor: pointer;
    }
    svg {
      height: 30px;
      width: 30px;
      cursor: pointer;
      z-index: 99;
      transition: transform 0.3s;
      position: absolute;
      margin-bottom: 1%;
      &:hover {
        height: 37px;
        width: 37px;
        transform: translateY(-5px);
        transition-duration: 0.3s;
      }
    }
    .dock_text {
      margin-top: auto;
    }
}
