$scrollColor: rgba(0, 0, 0, 0.7);
$firstColor: #000000a6;

.Modal {
	display: none;
	position: fixed;
	z-index: 1;
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
}

.ModalContent {
	background: #f4f4f4;
	margin: 6% auto;
	width: 80%;
	box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2),
							0 7px 20px 0 rgba(0, 0, 0, 0.17);
	animation-name: flipInX;
	animation-duration: 1s;
	background: #1e293a3a;
	height: 500px;
	border-radius: 15px;
	backdrop-filter: blur(25px);
	-moz-backdrop-filter: blur(25px);
	-webkit-backdrop-filter: blur(25px);
	color: white;
	overflow: auto;
		.ModalBody {
			padding: 10px 20px;
		}
		h1 {
			margin: 0;
			padding-top: 5px;
		}
		.close-btn {
			color: #fff;
			float: right;
			font-size: 30px;
			padding-right: 20px;
			&:hover {
				color: #f6d587;
				cursor: pointer;
				text-decoration: none;
			}
			&:focus {
				color: #f6d587;
				cursor: pointer;
				text-decoration: none;
			}
		}
		.row {
			margin-top: 0px;
		}
}

.overlay {
	display: block;
	position: fixed;
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
	z-index: 0;
	// background-color: rgba(0, 0, 0, 64%);
	visibility: hidden;
	opacity: 0;
	-webkit-transition: all 0.3s ease 0s;
	-o-transition: all 0.3s ease 0s;
	transition: all 0.3s ease 0s;
}

.visible {
	visibility: visible!important;
	opacity: 1;
}

.closeAnimation{
	animation-name: bounceOutRight;
	animation-duration: 1s;
}

.ModalShow {
	display: block;
}

::-webkit-scrollbar {
	width: .3rem;
	height: .3rem;
}

::-webkit-scrollbar-track {
	background: transparent;
}

::-webkit-scrollbar-thumb {
	background: $scrollColor;
	transition: .5s;
	border-radius: 20px;
		&:hover {
			background: $firstColor;
			transition: .5s;
		}
}