/*************************
*******Service Page******
**************************/
.skills {
  padding-top: 20px;
}

.logos {
  max-width: 90%;
  width: 100%;
  margin-bottom: 25px;
}

.img-responsive {
  border-radius: 10px;
}
#recent-projects{
  padding: 85px 0;
}

.recent-projects .team-single{
  height: 355px;
}

.recent-projects p.padding-bottom{
  padding-bottom: 50px;
}

.choose{
  padding-top: 30px;
}

#company-information.choose .padding-top{
  padding-top: 50px;
}

#company-information h2{
  margin-bottom: 25px;
  margin-top: 0;
  text-align: center;
  font-size: 25px;
}

ul.elements{
  list-style: none;
  margin: 0;
  padding: 0;
}

ul.elements li{
  margin: 6px 0;
}

ul.elements li{
  color:#C03035;
  display: inline-block;
  margin-right: 10px;
}

#company-information h1.margin-bottom{
  margin-bottom: 26px;
}


.single-service{
  overflow: hidden;
  display: block;
}

.single-service img {
  margin-bottom: 35px;
}

.single-service .fold {
  margin-bottom: 35px;
}

.single-service h2 {
  margin-top: 0;
  margin-bottom: 15px;
}

.scaleIn {
  -webkit-animation-name: scaleIn;
  animation-name: scaleIn;
}

.uk-card-body {
  display: flow-root;
  padding: 30px 30px
}

.uk-card-body>:last-child,.uk-card-header>:last-child,.uk-card-footer>:last-child {
  margin-bottom: 0
}

.uk-card-body>.uk-nav-default {
  margin-left: -30px;
  margin-right: -30px
}

.uk-card-hover:not(.uk-card-default):not(.uk-card-primary):not(.uk-card-secondary):hover {
  background: #111;
  box-shadow: 0 14px 25px rgba(0,0,0,0.16)
}

.uk-card-default {
  background: #111720;
  color: #ccc;
  box-shadow: 0 5px 15px rgba(0,0,0,0.08);
  min-height: 437px;
  margin-top: auto;
  &:hover {
    background-color: #11172091;  
  }
}

.uk-card-default .uk-card-title {
  color: #fff
}

.uk-card-default.uk-card-hover:hover {
  background-color: #1e1e1e;
  box-shadow: 0 14px 25px rgba(0,0,0,0.16)
}

.uk-card-default .uk-card-header {
  border-bottom: 1px solid #2a2a2a
}

.uk-card-default .uk-card-footer {
  border-top: 1px solid #2a2a2a
}

.uk-grid+.uk-grid,.uk-grid>.uk-grid-margin,*+.uk-grid-margin {
  margin-top: 30px
}

.uk-grid-divider>:not(.uk-first-column)::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  border-left: 1px solid #2a2a2a
}

.uk-grid-divider>:not(.uk-first-column)::before {
  left: 30px
}