button {
  cursor: pointer;
}

.default_contact {
	margin: auto;
}

.pulse {
	// height: 50px;
	// width: 50px;
  margin: auto;
	// background: linear-gradient(#3a5173, #0c1117);
	border-radius: 50%;
	display: grid;
	place-items: center;
	font-size: 30px;
	color: #fff;
	z-index: 99;
	left: 3%;
	cursor: pointer;
}
.pulse a{
  color:#fff
}
.pulse:before,
.pulse:after{
  content: "";
  position: absolute;
  height: 80%;
  width: 80%;
  background-color: #33455f;
  border-radius: 50%;
  z-index: -1;
  opacity: 0.7;
}
.pulse:before{
  animation: pulse 2s ease-out infinite;
}
.pulse:after{
  animation: pulse 2s 1s ease-out infinite;
}
@keyframes pulse{
  100%{
      transform: scale(2.5);
      opacity: 0;
  }
}


.container .sap_tel , .container .sap_mail{
	display: flex;
}

.container_us{
	width: 100%;
	padding: 25px 40px 10px 40px;
	.row{
		margin-top: 0px;
	}
}

.container_us .text{
	font-size: 35px;
	font-weight: 600;
	text-align: center;
	background: -webkit-linear-gradient(right, #56d8e4, #9f01ea,#56d8e4, #9f01ea);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.container_us form{
	padding: 0px 0 0 0;
}

.container_us form .form-row{
	display: flex;
	margin: 80px 0;
}

form .form-row .input-data{
	width: 100%;
	height: 40px;
	position: relative;
}

form .form-row .textarea{
	height: 70px;
}
input{
	background: transparent;
	color: white;
}

label{
	color: white;
}

textarea{
	background: transparent;
	color: white;
}
.input-data input,
.textarea textarea{
	display: block;
	height: 100%;
	width: 100%;
	outline: none;
	border: none;
	font-size: 25px;
	border-bottom: 2px solid rgba(255, 255, 255, 0.12);
}


.textarea textarea{
	resize: none;
	padding-top: 10px;
	overflow: hidden;
}

.input-data input:focus ~ label,
.textarea textarea:focus ~ label,
.input-data input:valid ~ label,
.textarea textarea:valid ~ label{
	transform: translateY(-20px);
	color: #3066ac;
	font-size: 14px;    
}

.input-data label{
	position: absolute;
	bottom: 10px;
	font-size: 16px;
	pointer-events: none;
	transition: all 0.3s ease;
}

.textarea label{
	width: 100%;
	bottom: 40px;
	background: transparent;
}

.input-data .underline{
	position: absolute;
	bottom: 0;
	height: 2px;
	width: 100%;
}

.input-data .underline::before,
.input-data .underline::after{
	position: absolute;
	content: "";
	height: 100%;
	width: 100%;
	background: #184075;
	transform: scaleX(0);
	transform-origin: center;
	transition: transform 0.3s ease;
}

.input-data input:focus ~ .underline::before, .underline::after,
.textarea textarea:focus ~ .underline::before, .underline::after,
.input-data input:valid ~ .underline::before, .underline::after,
.textarea textarea:valid ~ .underline::before, .underline::after{
	transform: scaleX(1);
}

.submit-btn .input-data{
	height: 45px!important;
	width: 25%!important;
	overflow: hidden;
}

.icons{
	width: 85px;
	height: 45px;
}

.txt_main{
	padding: 0 20px!important;
	text-indent: 30px;
}

.button.type3 {
	color: #ffffff;
	background-color: transparent!important;
	border: none;
	font-size: 17px;
	justify-content: center;
	margin-top: 10px;
	padding: 0 25px;
	width: 100%;
	outline: none;
}
.button.type3.type3::after, .button.type3.type3::before {
	content: "";
	display: block;
	position: absolute;
	width: 20%;
	height: 20%;
	border: 2px solid;
	transition: all 0.6s ease;
	border-radius: 2px;
}
.button.type3.type3::after {
	bottom: 0;
	right: 0;
	border-top-color: transparent;
	border-left-color: transparent;
	border-bottom-color: #435a6b;
	border-right-color: #435a6b;
}
.button.type3.type3::before {
	top: 0;
	left: 15px;
	border-bottom-color: transparent;
	border-right-color: transparent;
	border-top-color: #435a6b;
	border-left-color: #435a6b;
}
.button.type3.type3:hover:after, .button.type3.type3:hover:before {
	border-bottom-color: #435a6b;
	border-right-color: #435a6b;
	border-top-color: #435a6b;
	border-left-color: #435a6b;
	width: 80%;
	height: 80%;
}

a {
	color: white;
}