.normal-img {
  width: 60%;
  margin: auto;
}

.mike-img {
  width: 65%;
  margin: auto;
}

.mekan-img {
  width: 50%;
  margin: auto;
}

.title{
	font-size: 30px;
	font-weight: 700;
	color: #f6ca77;
	text-transform: uppercase;
	margin: 0 0 5px 0;
}
.post{
	display: block;
	font-size: 17px;
	color: #878484;
	text-transform: capitalize;
	margin-bottom: 10px;
}
.row{
	display: flex;
}
.leftSide{
  animation: bounceInLeft;
  animation-duration: 2s;
}
.rightSide{
  animation: bounceInRight;
  animation-duration: 2s;
}