.tilesWrap {
	padding: 0;
	margin: 15px auto;
	list-style: none;
	text-align: center;
  li{
    &::before{
      content: '';
      position: absolute;
      top: -2px;
      left: -2px;
      right: -2px;
      bottom: -2px;
      z-index: -1;
      background: #fff;
      transform: skew(2deg, 2deg);
    }
    &::after{
      content: '';
      position: absolute;
      width: 40%;
      height: 100%;
      left: 0;
      top: 0;
      // background: rgb(16 22 31 / 14%);
    }
    &:nth-child(1)::before{
      background: #c21500;
      background: -webkit-linear-gradient(to right, #ffc500, #c21500);
      background: linear-gradient(to right, #ffc500, #c21500);
    }
  }
  .image-settings{
    display: inline-block;
    width: 80%;
    min-width: 200px;
    max-width: 80%;
    position: relative;
    vertical-align: top;
    margin: 10px;
    font-family: "helvetica", san-serif;
    min-height: 25vh;
    // background: #10161f;
    // border: 1px solid #252727;
    text-align: left;
  
  }
  .text-settings{
    display: inline-block;
    /* width: 20%; */
    min-width: 200px;
    max-width: 80%;
    position: relative;
    vertical-align: top;
    margin: 10px;
    font-family: "helvetica", san-serif;
    min-height: 25vh;
    background: #10161f;
    border: 1px solid #252727;
    text-align: left;
    width: 100%;
    height: 53vh;
    h1{
      font-size: 114px;
      margin: 0;
      position: absolute;
      opacity: 0.2;
      top: 50px;
      right: 10px;
      transition: all 0.3s ease-in-out;
      color: #cf3b0c;
    }
    h3{
      font-size: 20px;
      color: #b7b7b7;
      margin-bottom: 5px;
      text-align: center;
    }
    p{
      font-size: 16px;
      line-height: 25px;
      color: #b7b7b7;
      margin-top: 15px;
      padding: 15px;
    }
  }
  a{
    cursor: pointer;
  }
}

.tilesWrap li:hover button {
	transform: translateY(5px);
	opacity: 1;
  bottom: 0;
}

.tilesWrap li:hover h1 {
	top: 0px;
	opacity: 0.6;
}
