.BoxButton {
	background: none;
	border: 1.5px solid #ffffff;
	padding: 0;
	color: #fff;
	width: 200px;
	height: 180px;
	margin: 5px;
	// margin-right: 10px;
	// margin-bottom: 10px;
	cursor: pointer;
		&:hover {
			background: #3336;
		}
		svg {
			display: block;
			height: 2rem;
			width: 2rem;
			margin: auto;
			margin-bottom: 1rem;
		}
		span {
			padding-top: 15px;
			font-size: 20px;
			width: 100%;
			letter-spacing: 1px;
		}
}
